import { useEffect, useState } from "react";
import axios from "axios";
import {  useLocation } from "react-router-dom";
import "../web.css";

function Profile() {
  const location = useLocation();
  const profilename = location.state.profilename;

  const [profile, setProfile] = useState(null);
  const [selectedUser, setSelectedUser] = useState(null);

  const profilePic = (name) => {
    axios
      .get("https://minilinked-in.onrender.com/api/posts/profile", {
        params: {
          firstname: name,
        },
      })
      .then((result) => {
        setProfile(result.data);
        setSelectedUser(name);
      })
      .catch((err) => {
        console.log("error in getting profile", err);
      });
  };

  useEffect(() => {
    profilePic(profilename);
  }, [profilename]);

  return (
    <div>
      <div className="profile flex">
        {selectedUser === profilename && profile && (
          <div className="profilepic">
            <strong>
              ID : <p>{profile._id}</p>
            </strong>
            <strong>
              NAME :
              <p>
                {profile.firstname} {profile.lastname}
              </p>
            </strong>
            <strong>
              EMAIL :<p>{profile.email}</p>
            </strong>
            <strong>
              Username :<p>{profile.username}</p>
            </strong>
            <strong>
              BIO :<p>{profile.bio}</p>
            </strong>
          </div>
        )}
      </div>
    </div>
  );
}

export default Profile;
